<template>
	<div id="haodian-settle">
		<public-header title="商户入驻"></public-header>
		<div class="content">
			<van-cell-group>
				<van-field
					v-model="value"
					label="店铺名称"
					placeholder="请填写店铺名称"
					input-align="right"
				/>
				<van-field
					v-model="value"
					label="店铺电话"
					placeholder="请填写店铺电话"
					input-align="right"
				/>
				<van-cell title="行业分类">
					<div slot="right-icon">
						<span>请选择</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-cell title="营业时间">
					<div slot="right-icon">
						<span>请选择</span>
						<van-icon name="arrow"></van-icon>
					</div>
				</van-cell>
				<van-field
					v-model="value"
					label="店铺地址"
					placeholder="请填写店铺地址"
					input-align="right"
				/>
				<van-field
					v-model="value"
					label="店铺简介"
					type="textarea"
					placeholder="请填写店铺简介"
					rows="1"
					input-align="right"
					autosize
				/>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-cell>
					<div class="upload-img">
						<div class="goods-img">
							<div>添加店铺图标</div>
						</div>
						<uploader :max="4" :value="thumbs" @onGetUrl="onChangeThumbs" @delete="onChangeThumbs"></uploader>
					</div>
				</van-cell>
				<van-cell>
					<div class="upload-img">
						<div class="goods-img">
							<div>添加店铺幻灯片</div>
						</div>
						<uploader :max="4" :value="thumbs" @onGetUrl="onChangeThumbs" @delete="onChangeThumbs"></uploader>
					</div>
				</van-cell>
				<van-cell>
					<div class="upload-img">
						<div class="goods-img">
							<div>添加店铺详情图</div>
						</div>
						<uploader :max="4" :value="thumbs" @onGetUrl="onChangeThumbs" @delete="onChangeThumbs"></uploader>
					</div>
				</van-cell>
			</van-cell-group>
			<van-cell-group class="margin-10-t">
				<van-field
					v-model="value"
					label="申请人"
					placeholder="请填写申请人"
					input-align="right"
				/>
				<van-field
					v-model="value"
					label="联系电话"
					placeholder="请填写联系电话"
					input-align="right"
				/>
			</van-cell-group>
			<div class="tips flex">
				<i class="icon" :class="{'icon-check': check == '1'}"></i>
				<span>我已阅读并同意</span>
				<span class="c-danger">《商家入驻协议》</span>
			</div>
			<div class="padding-15">
				<van-button size="normal" block class="bg-danger">提交</van-button>
			</div>
		</div>
	</div>
</template>

<script>
	import PublicHeader from '@/components/header'
	import Uploader from '@/components/uploader'
	export default {
		components: {
			PublicHeader,
			Uploader
		},
		data() {
			return {
				value: '',
				check: 0
			}
		}
	}
</script>

<style>
	#haodian-settle .content .upload-img #uploader{
		padding: 0;
	}
	#haodian-settle .content .upload-img .goods-img{
		padding-bottom: 10px;
		color: #333;
		font-size: 14px;
	}
	#haodian-settle .content .tips{
		padding: 15px 10px 0;
		font-size: 14px;
		color: #999;
	}
	#haodian-settle .content .tips .icon{
		width: 20px;
		height: 20px;
		border-radius: 50%;
		border: 1px solid #ccc;
		text-align: center;
		line-height: 18px;
		margin-right: 5px;
	}
	#haodian-settle .content .tips .icon.icon-check{
		color: #fff;
		background-color: #ff2d4b;
		border-color: #ff2d4b;
	}
</style>


